@keyframes backgroundAnim {
  from {
    background-position: left bottom;
  }
  to {
    background-position: right bottom;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
